import React from 'react'
import { Link } from 'react-router-dom'
import StoreHOC from '../Store/StoreHOC'

import './veiculos.scss'

const menuCards = [
    {
        title: 'Cadastro de Veculos',
        description: 'Cadastrar um novo veculo no sistema',
        imageUrl: '/images/add_new.jpg',
        link: '/cadastro'
    },
    {
        title: 'Alterao de dados',
        description: 'Alterar dados de um veculo',
        link: '/altDados',
        imageUrl: '/images/pen.png'
    },
    {
        title: 'Seguros',
        description: 'Cadastro e atualizao de seguro',
        link: '/seguros',
        imageUrl: "/images/car_insurance2.png"
    },
    {
        title: 'Laudos',
        description: 'Laudos de segurana veicular',
        link: '/laudos',
        imageUrl: "/images/laudos.png"
    },
    {
        title: 'Baixa',
        description: 'Baixa de veculos',
        link: '/baixaVeiculo',
        imageUrl: "/images/remove_doc.png"
    },
    {
        title: 'Configuraes',
        description: 'Chassi, carrocerias, seguradoras, etc',
        link: '/config',
        imageUrl: "/images/config2.jpg"
    }
]

function VeiculosHome(props) {

    const { match, user } = props

    return (
        <div>
            <section className="jumbotron">
                <h1>
                    Veculos
                </h1>
                <p>
                    Selecione uma das opes abaixo.
                </p>
            </section>
            <section className="card">
                {
                    menuCards.map(({ title, link, imageUrl, description }, i) =>
                        user?.role !== 'admin' && link === '/config' ?      //No renderiza a opo "Configuraes" para usurios sem permisso
                            null :
                            <Link to={match.url + link} key={i} className="card__container">
                                <span className="card__image"> <img src={imageUrl} alt={title} /> </span>
                                <div className="card__text">
                                    <h3 >{title}</h3>
                                    <p > {description}</p>
                                </div>
                            </Link>
                    )}
            </section>
        </div >
    )
}

const collections = []
export default StoreHOC(collections, VeiculosHome)